.index {
  max-width: 100vw;
  margin: 0 auto;
}
.tab-panel-container {
  min-height: calc(100vh - 96px);
}
.tab-panel {
  margin-bottom: 60px;
  position: relative;
  height: 100%;
}
.tab-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 105;
  width: 100%;
  background: #fff;
}



